import PropTypes from 'prop-types';
import { useState } from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';

export default function DownloadDialog({
  open,
  onClick,
  onClose,
  sizes,
  collageType,
}) {
  const [value, setValue] = useState('');
  const handleDownload = () => {
    console.log(sizes[value].width, sizes[value].height, onClick);
    onClick(sizes[value].width, sizes[value].height);
    setValue('');
  };

  const handleClose = () => {
    setValue('');
    onClose();
  };
  console.log('download value', value);
  return (
    <Dialog open={open} fullWidth>
      <DialogTitle sx={{ textAlign: 'center' }}>Download Collage</DialogTitle>
      <DialogContent
        dividers
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
        }}
      >
        <DialogContentText sx={{ display: 'inline', pr: 1 }}>
          Choose collage size
        </DialogContentText>
        <FormControl fullWidth sx={{ maxWidth: '60%' }}>
          <InputLabel id="demo-simple-select-label">Download Size</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            defaultValue="A4"
            value={value}
            label="Download Size"
            onChange={(event) => setValue(event.target.value)}
            inputProps={{
              sx: {
                fontWeight: 'bold',
                color: 'gray',
                alignItems: 'end',
                display: 'flex',
              },
            }}
          >
            {'downloadSizes' in sizes[collageType] ? (
              sizes[collageType].downloadSizes.map((sz) => (
                <MenuItem key={sizes[sz].text} value={sz}>
                  {sizes[sz].icon}
                  {sizes[sz].text}
                </MenuItem>
              ))
            ) : (
              <MenuItem key={sizes[collageType].text} value={collageType}>
                {sizes[collageType].icon}
                {sizes[collageType].text}
              </MenuItem>
            )}
          </Select>
        </FormControl>
      </DialogContent>
      <DialogActions>
        <Button
          variant="outlined"
          disabled={value.length === 0}
          onClick={handleDownload}
        >
          Download
        </Button>
        <Button variant="text" onClick={handleClose}>
          Cancel
        </Button>
      </DialogActions>
    </Dialog>
  );
}

DownloadDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  onClick: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired,
  sizes: PropTypes.objectOf(
    PropTypes.shape({
      width: PropTypes.number.isRequired,
      height: PropTypes.number.isRequired,
      text: PropTypes.string.isRequired,
      icon: PropTypes.node.isRequired,
      downloadSizes: PropTypes.arrayOf(PropTypes.string),
    })
  ).isRequired,
  collageType: PropTypes.string.isRequired,
};
